/* App.css */

body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f4f4f4;
}

.app-container {
  padding: 1rem;
  text-align: center;
  background: #ffffff;
  min-height: 100vh;
}

.app-header {
  font-size: 1.6rem;
  font-weight: bold;
  margin-bottom: 1rem;
  color: #111;
}

.msr-title {
  color: #1e90ff;
}

.search-bar {
  padding: 10px;
  width: 80%;
  max-width: 400px;
  margin: 10px auto 20px;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 8px;
}

.table-container {
  overflow-x: auto;
  margin-bottom: 2rem;
}

.leaderboard-table {
  width: 100%;
  border-collapse: collapse;
  font-size: 0.9rem;
  min-width: 800px;
}

.leaderboard-table th,
.leaderboard-table td {
  border: 1px solid #ccc;
  padding: 8px;
  text-align: center;
}

.highlight-msr {
  background-color: #1e90ff;
  color: white;
  font-weight: bold;
}

.page-indicator {
  margin-bottom: 1rem;
  font-size: 0.85rem;
  color: #888;
}

.page-content {
  font-size: 1.2rem;
  margin-top: 2rem;
  color: #444;
}












